<template>
  <div class="board">
    <div class="frame-wrapper" :style="frameSize">

      <p v-if="solved && userHasMoved" class="win">
        <span>¡Enhorabuena has completado el puzzle!!</span>
        <span>Pulsa en premios para comprobar si te ha tocado una fantástica recompensa. </span>
      </p>

      <p v-if="farFromBeacon" class="win">
        <span>¡Ops estás fuera del alcance!</span>
        <span>Para desbloquear la pieza acércate a uno de los puntos de refrescos, neveras y expositores fuera de la zona habitual de refrescos.</span>
      </p>

      <p v-if="!locked && !userHasMoved && !reorderedTiles" class="win">
        <span>¡Ordena las piezas y podrás ganar una fantástico premio!</span>
      </p>

      <div class="frame" :style="frameSize">
        <Tile v-for="tile in tiles"
          :key="tile.position"
          :tile="tile"
          @clicked="clickedTile"
          ref="tiles"
        />
      </div>

    </div>
  </div>
</template>

<script>
import sample from 'lodash.sample'
import Tile from './Tile'
import axios from 'axios'
import 'url-search-params-polyfill'

var instance = axios.create({
  /* global API_BASE_URL */
  baseURL: API_BASE_URL
})

export default {
  components: { Tile },

  data () {
    return {
      image: null,
      size: {
        horizontal: 0,
        vertical: 0
      },
      tiles: [],
      tileSize: {
        width: 0,
        height: 0
      },
      moves: 0,
      reorderedTiles: false,
      farFromBeacon: false
    }
  },

  watch: {
    locked (isLocked, wasLocked) {
      if (!isLocked && wasLocked) {
        this.performShuffle()
      }
    },

    farFromBeacon (a, b) {
      let x = this
      if (a && !b) {
        setTimeout(() => {
          x.farFromBeacon = false
        }, 5000)
      }
    }
  },

  computed: {
    viewportHeight () {
      return Math.max(document.documentElement.clientWidth, window.innerWidth || 0)
    },

    viewportWidth () {
      return Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
    },

    locked () {
      return this.tiles.some(tile => tile.isLocked)
    },

    frameSize () {
      return {
        width: `${this.tileSize.width * this.size.horizontal}px`,
        height: `${this.tileSize.height * this.size.vertical}px`
      }
    },

    userHasMoved () {
      return this.moves > 0
    },

    currentUser () {
      return this.getQueryParams().player_id
    },

    currentImage () {
      // return this.getQueryParams().image
      return 'new_otter'
    },

    totalTiles () {
      return this.size.horizontal * this.size.vertical
    },

    solved () {
      if (!this.tiles.length) {
        return false
      }

      if (this.locked) {
        return false
      }

      if (!this.userHasMoved || localStorage.image !== this.currentImage) {
        return false
      }

      for (let i = 0; i < this.totalTiles; ++i) {
        if (this.tiles[i].styles.order !== this.tiles[i].position) {
          return false
        }
      }

      this.obtainPincode()

      return true
    }
  },

  methods: {
    obtainPincode () {
      this.unlock({position: 0})
    },

    performShuffle () {
      let x = this
      setTimeout(() => {
        x.shuffleTiles()
      }, 5000)
    },

    getQueryParams (str) {
      return (str || document.location.search).replace(/(^\?)/, '').split('&').map(function (n) {
        n = n.split('=')
        this[n[0]] = n[1]
        return this
      }.bind({}))[0]
    },

    backupTiles () {
      localStorage.tiles = JSON.stringify(this.tiles)
      localStorage.image = this.currentImage
    },

    restoreTiles () {
      if (localStorage.image === this.currentImage) {
        if (localStorage.tiles) {
          this.tiles = JSON.parse(localStorage.tiles)
          this.reorderedTiles = true
        }
      }
    },

    start ({ image, size }) {
      this.size = size
      this.image = image
      const img = new Image()
      img.onload = () => {
        this.tileSize.width = Math.floor(this.viewportHeight / size.horizontal)
        this.tileSize.height = Math.floor(this.viewportWidth / size.vertical)
        this.generateTiles()
      }
      img.src = image
    },

    generateTiles () {
      this.tiles = []

      instance.get('/api/captures/by_user/' + this.currentUser).then(response => {
        let unlocked = response.data.list.filter(unlock => unlock.image === this.currentImage).map(unlock => Number(unlock.tile))

        for (let i = 0; i < this.totalTiles; ++i) {
          this.tiles.push({
            styles: {
              background: i === 0 ? 'transparent' : `url(${this.image})`,
              backgroundPositionX: `-${(i % this.size.horizontal) * this.tileSize.width}px`,
              backgroundPositionY: `-${Math.floor(i / this.size.horizontal) * this.tileSize.height}px`,
              width: `${this.tileSize.width}px`,
              height: `${this.tileSize.height}px`,
              order: i
            },
            position: i,
            isEmpty: i === 0,
            isLocked: !unlocked.includes(i) ^ i === 0
          })
        }

        if (!this.locked && !localStorage.tiles) {
          this.performShuffle()
        } else {
          this.restoreTiles()
        }
      })
    },

    shuffleTiles () {
      for (let i = 0, j = this.totalTiles * 5; i < j; ++i) {
        const emptyTile = this.tiles.find(t => t.isEmpty)
        const movableTiles = this.tiles.filter(t => {
          return this.getAdjacentOrders(emptyTile).includes(t.styles.order)
        })
        this.switchTiles(emptyTile, sample(movableTiles))
      }
      this.reorderedTiles = true
    },

    unlock (tile) {
      var params = new URLSearchParams()
      params.append('user', this.currentUser)
      params.append('tile', tile.position)
      params.append('image', this.currentImage)

      instance.post('/api/captures', params).then(response => {
        console.log(response)
        if (response.data.status === 'ok') {
          tile.isLocked = false
        } else {
          this.farFromBeacon = true
        }
      })
    },

    clickedTile (tile) {
      if (tile.isEmpty) {
        return
      }

      if (tile.isLocked) {
        this.unlock(tile)
        return
      }

      if (this.locked) {
        return
      }

      const target = this.tiles.find(t => {
        return t.isEmpty && this.getAdjacentOrders(tile).indexOf(t.styles.order) > -1
      })

      target && this.switchTiles(target, tile)
      this.moves += 1

      this.backupTiles()
    },

    switchTiles (a, b) {
      [a.styles.order, b.styles.order] = [b.styles.order, a.styles.order]
    },

    getAdjacentOrders (tile) {
      const pos = tile.styles.order
      return [
        pos % this.size.horizontal ? pos - 1 : null,
        (pos + 1) % this.size.horizontal ? pos + 1 : null,
        pos - this.size.horizontal,
        pos + this.size.horizontal
      ]
    }
  }
}
</script>

<style lang="scss">
.frame-wrapper {
  margin: 0 auto;
  position: relative;
  // box-shadow: 0 0 0px 10px;

  p.win {
    span {
      display: block
    }

    span:first-child{
      font-weight: bold;
      margin-bottom: 15px
    }

    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 14px;
    margin: 0 0;
    background: rgba(0, 0, 0, .5);
    background-image: url('../assets/notification.png') ;
    background-size: contain;
    padding: 0 15px;
    text-align: center;
    z-index: 999;
    // text-transform: uppercase;
  }

}

.frame {
  display: flex;
  flex-wrap: wrap;
  background: #fff;
  background-size: cover;
}

</style>



